@import "./color";
// @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

*{
   padding:0;
   margin:0;
   box-sizing: border-box;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   color:var(--prm-clr);
   line-height: 1.5;
   transition: background 0.3s ease;
   letter-spacing: 0.07rem;
   font-weight: var(--weight);
}


html{
    font-size: 62.5%;
}
html, #root, main{
   min-height:100%;
}

body{
   background:var(--body-bg);
   max-width: 220rem;
   margin:auto;
}

.accent{
   color:var(--secondary-clr);
}

.error{
   color:red;
}

.edit_mode{
    position: relative;
    .edit_modal{
       top:50% !important;
       left:50% !important;
       transform: translate(-50%,-50%);
       background: var(--modal-bg);
       width:90% !important;
       height: 80% !important;
       max-width:50rem;
       max-height: 50rem;
       textarea{
          height: 80%;
       }
       span{
          position:absolute;
          background: rgba(2, 61, 95,0.3);
          transform:translate(-50%,-50%);
          border-radius:50%;
          pointer-events: none;
          animation:animate_ripple 0.5s linear infinite;
      }
    }
    header{
      z-index: unset;
    }

    .note{
       pointer-events: none;
    }
    .edit_modal_wrapper{
       display: flex !important;
    }
}


a,button{
   text-decoration: none;
   border:none;
   cursor: pointer;
   background: inherit;
   font-size: clamp(1.6rem, 1.7rem, 1.7vw);
   font-weight: 500;
}
html{
   ::selection{
      background:var(--secondary-clr);
      color:hsl(0, 0%, 91%);
   }
}


// animation of loading in hero
// doing it here because hero_img is lossing the animation
.hero > * {
   animation: animateLoad calc(1.3s + (0.2s  * var(--order))) ease-in forwards;
}

.show_nav_overlay{
      display:block !important;
}
.show_note_menu_overlay{
   background:rgba(0,0,0, 0.1) !important;
   display:block !important;
}
.active_link{
   text-decoration: underline;
   color:var(--secondary-clr);
}